@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300italic,600,600italic");
@import url("https://fonts.googleapis.com/css?family=IM+Fell+DW+Pica+SC");
.flex_block {
  display: flex;
}

.block-InfoPod {
  padding: 2em 0;
}
.block-InfoPod .flex_block {
  justify-content: space-around;
}
.block-InfoPod .infopod {
  max-width: 30%;
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 32px;
}
.block-InfoPod p {
  text-align: center;
  padding: 0;
  margin: 0;
}
.block-InfoPod img {
  box-sizing: border-box;
  background: #8dca31;
  height: auto;
  width: calc(64px + 1em);
  max-width: 100%;
  padding: 0.5em;
  display: block;
  border-radius: 50%;
  border: solid 3px #fff;
}
.block-InfoPod ul {
  display: flex;
  padding: 0;
  margin: 0;
}
.block-InfoPod ul li {
  flex: 1;
}

.block-Callout {
  box-shadow: inset 0 0 min(10vw, 150px) #000;
  padding: 3rem 0;
  text-align: center;
}
.block-Callout h2,
.block-Callout p {
  padding: 0;
  margin: 0;
}
.block-Callout hr {
  border-top: 1px dotted #8dca31;
  margin: 1em 0;
}

header {
  padding: 1.5em 0;
  text-align: center;
}
header img {
  display: inline-block;
  margin: auto;
}

nav.main {
  border-bottom: 0.5em solid #8dca31;
}
nav.main a {
  position: relative;
  text-decoration: none;
  border: none;
  color: #fff;
  font-weight: 300;
}
nav.main .active a {
  font-weight: 600;
  color: #f7f7f7;
}
nav.main .active a::before {
  position: absolute;
  bottom: -0.75em;
  left: calc(50% - 0.5em);
  content: " ";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0.5em 0.5em 0.5em;
  border-color: transparent transparent #8dca31 transparent;
}
nav.main ul {
  padding: 0 0 0.5em 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
nav.main ul li {
  display: block;
  padding: 0;
  margin: 0;
  list-style: none;
}

.banner_image picture {
  display: block;
  height: 100%;
  width: 100%;
  position: relative;
}
.banner_image picture::after {
  box-shadow: inset 0 0 min(10vw, 150px) #000;
  content: " ";
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  width: 100%;
}
.banner_image img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
  position: relative;
}

#footer {
  border-top: #8dca31 solid 0.25em;
}

.project-pods {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2em;
  padding-bottom: 2em;
}
.project-pods .pod {
  box-sizing: border-box;
  padding: 0;
  display: flex;
}
.project-pods .pod .pod-details {
  padding-left: 1em;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.project-pods .pod .pod-details h2 {
  padding: 0 0 0.75em 0;
  margin: 0;
  line-height: 1em;
  font-size: 1.5em;
}
.project-pods .pod .pod-details p {
  font-size: 1em;
  line-height: 1.5em;
  padding: 0;
  margin: 0;
  flex: 1;
}
.project-pods .pod .pod-details div {
  text-align: right;
}
.project-pods .pod .pod-details div a {
  font-weight: bold;
}

:root {
  --max-width: 100%;
}

@media screen and (min-width: 576px) {
  :root {
    --max-width: 540px;
  }
}
@media screen and (min-width: 768px) {
  :root {
    --max-width: 720px;
  }
}
@media screen and (min-width: 992px) {
  :root {
    --max-width: 960px;
  }
}
@media screen and (min-width: 1200px) {
  :root {
    --max-width: 1140px;
  }
}
@media screen and (min-width: 1400px) {
  :root {
    --max-width: 1320px;
  }
}
.container, .block-InfoPod, nav.main ul {
  max-width: var(--max-width);
  margin: 0 auto;
}

.logo {
  max-width: 100%;
  height: auto;
}

body {
  padding: 0;
  margin: 0;
  background: #374244;
}
body.is-loading * {
  transition: none !important;
  animation: none !important;
}

body,
input,
select,
textarea {
  color: #f7f7f7;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16pt;
  font-weight: 300;
  line-height: 1.65em;
}

a {
  transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out, opacity 0.2s ease-in-out;
  color: #8dca31;
  text-decoration: none;
  border-bottom: dotted 1px;
}
a.no-b {
  border-bottom: none;
}
a svg {
  transition: fill 0.2s ease;
}
a:hover {
  color: #8dca31;
  border-bottom-color: transparent;
}
a:hover svg {
  fill: #8dca31;
}

strong,
b {
  font-weight: 600;
}

em,
i {
  font-style: italic;
}

p,
ul,
ol,
dl,
table,
blockquote {
  margin: 0 0 2em 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: inherit;
  font-weight: 600;
  line-height: 1.75em;
  margin-bottom: 1em;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
  text-decoration: none;
}
h1 em,
h2 em,
h3 em,
h4 em,
h5 em,
h6 em {
  font-style: normal;
  font-weight: 300;
}

h1 {
  text-decoration: underline #8dca31;
}

h2 {
  font-size: 1.75em;
  letter-spacing: -0.025em;
}

h3 {
  font-size: 1.2em;
  letter-spacing: -0.025em;
}

sub {
  font-size: 0.8em;
  position: relative;
  top: 0.5em;
}

sup {
  font-size: 0.8em;
  position: relative;
  top: -0.5em;
}

hr {
  border-top: solid 1px #e0e0e0;
  border: 0;
  margin-bottom: 1.5em;
}

blockquote {
  border-left: solid 0.5em #e0e0e0;
  font-style: italic;
  padding: 1em 0 1em 2em;
}

section.special,
article.special {
  text-align: center;
}